.contact-box{
    border-radius: 0.5em;
    box-shadow:inset 0 0 1.5em rgba(0, 0, 0, 0.5); 
    border: 0.2em solid #c40c0c;
}
.contact-box:hover{
    box-shadow:inset 0 0 2em rgba(0, 0, 0, 0.5); 
}

.main-box{
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.5);
}

.box{
    border-top-left-radius: 0.4em;
    border-top-right-radius: 0.4em;
    box-shadow: 1.5em 1.5em 2em -1px #c0c0c079;
}

.box-desc{
    border-bottom-left-radius: 0.4em;
    border-bottom-right-radius: 0.4em;
    box-shadow: 1.5em 1.5em 2em -1px #c0c0c079;
    
}

.experience-img{
    border-radius: 0.2em;
}

.bean{
    border-top-right-radius: 0.4em;
    filter: drop-shadow(0 0 1.5em #000000); 
}

.box-shadow{
    box-shadow: 0 0 2em #c40c0c;
    border-radius: 5em;
    width: 3.5em;
    height: 3.5em;
    display: flex; 
    justify-content: center;
    align-items: center;
}
.box-shadow:hover{
    box-shadow: 0 0 3em #c0c0c0;
}

.certi{
    box-shadow: 2em 2em 1em #c40c0c4f;
}

.cert{
    box-shadow: 0 0 1em #0c01019d;
}

